
@font-face {
  font-family: 'agathoregular';
  src: url('./Assets/agatho_regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;

}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

h1 {
  font-family: 'agathoregular', serif;
}

h2 {
  font-family: 'Karla', sans-serif;
}

p {
  font-family: 'Karla', sans-serif;
}